/* navbar itself */
.navbar {
    height: 50px;
    background: rgb(255,215,0);
    background: radial-gradient(circle, rgba(255,215,0,1) 0%, rgba(255,180,0,1) 50%, rgba(255,250,0,1) 100%);
    position: relative;
}
.navbar > * {
    display: inline;
}

/* home-menu-linked-image */
.navbar > .icon {
    position: absolute;
    top: 3px;
    left: 3px;
    bottom: 3px;
}
.navbar > .icon > img {
    height: 100%;
}

/* middle aligned header */
.navbar > .title {
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 50%;
    transform: translateX(-50%);
    white-space: nowrap;
    font-size: 2em;
    margin: 0px;
}
